export class Attributes<T> {
  constructor(private data: T) {}

  // K can only ever be one of the keys of T
  get = <K extends keyof T>(key: K): T[K] => {
    return this.data[key];
  };

  getAll(): T {
    return this.data;
  }

  set(update: T): void {
    Object.assign(this.data, update);
  }
}
